@import url('https://fonts.googleapis.com/css2?family=Didact+Gothic&family=Oswald&display=swap');

.wrapper1 {
    width: 100%;
    position: relative;
    border-top: 1px solid;
    margin-top: 100px;
    padding-top: 100px;
    display: flex;
    flex-direction: column;
    gap: 100px;
    align-items: center;
}
.wrapperItem1 {
    font-family: 'Oswald', sans-serif;
}
.head {
    position: absolute;
    margin-bottom: 300px;
    font-size: 35px;
    font-weight: 600;
    text-transform: uppercase;
    font-family: 'Oswald', sans-serif;
}

.wrapperItem1 {
    display: flex;
    justify-content: center;
    margin-top: 100px;
    flex-wrap: nowrap;
}

.block {
    width: 250px;
    border-left: 1px solid rgba(53, 53, 53, 0.186);
    border-right: 1px solid rgba(53, 53, 53, 0.186);
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    & h2 {
        font-size: 50px;
        font-weight: 600;
        display: flex;
        justify-content: flex-end;
        align-items: end;
        & span {
            font-size: 20px;
            position: relative;
            bottom: 5px;
        }
    }
    & p {
        text-align: center;
        color: rgba(124, 124, 124, 0.46);
    }
    margin-bottom: 100px;
}


@media only screen and (max-width: 943px){
    .block{
        width: 12rem;
    }
}
@media only screen and (max-width: 630px){
    .block{
        width: 8rem;
        & h2 {
            font-size: 35px;
            font-weight: 600;
            display: flex;
            justify-content: flex-end;
            align-items: end;
            & span {
                font-size: 15px;
            }
        }
    }
}
@media only screen and (max-width: 510px){
    .wrapper1 {
        margin-top: 0;       
        padding-top: 60px;
    }
    .block{
        width: 5.5rem;
        height: 140px;
        & h2 {
            font-size: 25px;
            font-weight: 600;
            display: flex;
            justify-content: flex-end;
            align-items: end;
            & span {
                font-size: 12px;
                position: relative;
                top: 0;
                padding-left: 2px;
            }
        }
        & p {
            width: 70px;
            font-size: 10px;
        }
    }
}

@media only screen and (max-width: 321px){
    .head {
        font-size: 30px;
    }
    .block{
        width: 5rem;
        & h2 {
            font-size: 20px;
            font-weight: 600;
            display: flex;
            justify-content: flex-end;
            align-items: end;
            & span {
                font-size: 12px;
                position: relative;
                top: 0;
                padding-left: 2px;
            }
        }
        & p {
            font-size: 10px;
        }
    }
}