@media only screen and (max-width: 943px){

.dropdown {
  position: relative;
    // top: 50px;
  margin: 0 auto;
  width: 220px;
  // height: 100vh;
  // border: 1px solid #666;
 background: #ffffff;

}

.dropdown__text {
 color: rgb(0, 0, 0);
 padding: 10px 16px;
  cursor: pointer;
height: auto;
font-weight: 400;
font-size: 20px;
}

.dropdown__text:hover {
  color: rgb(0, 0, 0);
  font-weight: 600;
}

.dropdown__text:after {
  content: "";
  transition: all .3s;
  border: solid #ccc;
  border-width: 0 1px 1px 0;
float: right;
margin-top: 15px;
margin-right: 6px;
  padding: 5px;
  transform: rotate(-135deg);
    -webkit-transform: rotate(-135deg);
  // height: 200px;
}

.dropdown.active .dropdown__text:after {
margin-top:10px;
transform: rotate(45deg);
-webkit-transform: rotate(45deg);

}

.dropdown__items {
 position: relative;
visibility: hidden;
opacity: 0;
 max-height: 0px;
 transition: max-height 0.6s ease;
 font-size: 15px;
 color: #000000;
 font-weight: 400;
}


.dropdown.active .dropdown__items {
 visibility: visible;

  border-top: 1px solid #3c3c3c59;
height: auto;
 min-height: 360px;
  opacity: 1;
 transition: max-height 0.7s, opacity 3s, visibility 4s ease;



}

.dropdown__item {
  cursor: pointer;
 padding: 10px 18px;
 
} 

.dropdown__item:not(:last-child) {
  border-bottom: 1px solid #3c3c3c1a;
  
}



.dropdown__item:hover {
  font-weight: 500;
}
  
  .navbar {
    display: block;
    width: 100vw;
  }
  
  .nav-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 62px;
  }
  
  .navbar .menu-items {
    display: flex;
  }
  
  .navbar .nav-container li {
    list-style: none;
  }

  .menu-items {
    display: block;
    overflow: scroll;
  }
  
  .navbar .nav-container a {
    text-decoration: none;
    color: #0e2431;
    font-weight: 500;
    font-size: 1.2rem;
    padding: 0.7rem;
  }
  
  .navbar .nav-container a:hover{
      font-weight: bolder;
  }
  
  .nav-container {
    display: block;
    position: relative;
    height: 60px;
  }
  
  .nav-container .checkbox {
    position: absolute;
    display: block;
    height: 32px;
    width: 32px;
    top: 20px;
    right: 20px;
    z-index: 5;
    opacity: 0;
    cursor: pointer;

  }
  
  .nav-container .hamburger-lines {
    display: block;
    height: 26px;
    width: 32px;
    position: absolute;
    top: 17px;
    right: 20px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .nav-container .hamburger-lines .line {
    display: block;
    height: 4px;
    width: 100%;
    border-radius: 10px;
    background: #0e2431;
  }
  
  .nav-container .hamburger-lines .line1 {
    transform-origin: 0% 0%;
    transition: transform 0.4s ease-in-out;
  }
  
  .nav-container .hamburger-lines .line2 {
    transition: transform 0.2s ease-in-out;
  }
  
  .nav-container .hamburger-lines .line3 {
    transform-origin: 0% 100%;
    transition: transform 0.4s ease-in-out;
  }
  
  .navbar .menu-items {
    padding-top: 120px;
    // box-shadow: inset 0 0 2000px rgba(255, 255, 255, .5);
    background: #ffffff;
    height: 100vh;
    width: 1000px;
    transform: translate(150%);
    display: flex;
    flex-direction: column;
    margin-left: -130px;
    padding-left: 50px;
    transition: transform 0.5s ease-in-out;
    text-align: center;
    margin-right: -150px;
    
  }
  
  .navbar .menu-items li {
    margin-bottom: 1.2rem;
    font-size: 1.5rem;
    font-weight: 500;
  }
  
  .logo {
    position: absolute;
    top: -23px;
    left: 15px;
    font-size: 1.2rem;
    color: #0e2431;

    & .Logo{
      display: block;
      width: 160px;
      height: 80px; 
    }
  }
  
  .nav-container input[type="checkbox"]:checked ~ .menu-items {
    transform: translateX(0);
  }
  
  .nav-container input[type="checkbox"]:checked ~ .hamburger-lines .line1 {
    transform: rotate(45deg);
    
  }
  
  .nav-container input[type="checkbox"]:checked ~ .hamburger-lines .line2 {
    transform: scaleY(0);
  }
  
  .nav-container input[type="checkbox"]:checked ~ .hamburger-lines .line3 {
    transform: rotate(-45deg);
  }
  
  .nav-container input[type="checkbox"]:checked ~ .logo{
    display: none;
  }
  
  .navigationMenu ul li {
    display: none; 
    background-color: #ffffff;
    }
}


@media only screen and (max-width: 769px){
  .navbar .menu-items {
    margin-left: -100px;

  }
}

@media only screen and (max-width: 617px){
  .navbar .menu-items {
    margin-left: -30px;
  }
}

@media only screen and (max-width: 576px){
  .navbar .menu-items {
    margin-left: -100px;
    padding-right: 100px;
    padding-top: 100px;
}
}